import imgNick from '../../../assets/img/nick-calm.jpg';

export default (function() {
  const appContainer = document.getElementById('mainContent');

  function bind() {
    template();
  }

  function template() {
    appContainer.innerHTML = htmlView();
  }

  function htmlView() {
    return `
      <div class="offline">
        <h1 class="title">VOC EST OFFLINE!</h1>
        <p>Alguns recursos podem no estar disponveis. Mas no se preocupe fera!  s testar sua conexo e tentar de novo.</p>
        <div class="offline-image">
          <img src="${imgNick}"/>
        </div>
      </div>
    `;
  }

  return {
    create: bind,
  };
})();
